* {
  padding: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background: #f4f4f4;
}

img {
  width: 100%;
  object-fit: contain;
}

.cartpage {
  display: flex;
  max-width: 1300px;
  margin: 2rem auto;
  /* align-items: center; */
  gap: 1.5rem;
  padding-top: 9rem;
}

.cartpage h2 {
  margin-bottom: 1rem;
}

.cartpage_left {
  flex: 0.7;
  margin-right: 1rem;
  background: transparent;
  padding: 1rem;
}

.cartpage_right {
  flex: 0.3;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  height: fit-content;
}

.cartpage_right div {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 1rem;
}

.cartpage_right div:last-child {
  border: none;
}

.cartpage_info p {
  padding: 8px;
}

.cartpage_right div button {
  padding: 10px 17px;
  width: 100%;
  background: #171717;
  color: #f4f4f4;
  border: 1px solid #171717;
  cursor: pointer;
}

.cartpage_right div button:hover {
  opacity: 0.9;
}

.cartscreen_info > p{
  font-size: 1.5rem;
}

@media (max-width: 1320px) {
  .cartpage {
    max-width: 900px;
  }
}

@media (max-width: 960px) {
  .cartpage {
    max-width: 800px;
    flex-direction: column;
  }

  .cartpage_left {
    margin: 0rem;
  }

  .cartpage_right {
    margin: 1rem;
  }
}
