.cartitem {
  width: 100%;
  padding: 1rem;
  margin: 8px;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
  gap: 8px;
  background: #fff;
  border-radius: 2px;
  place-items: center;
}

.cartitem_name {
  text-decoration: none;
  color: #171717;
}

.cartitem_name > p {
  font-size: 1.3rem;
}

.cartitem_name:hover {
  color: #dc219e;
}

.cartitem_select {
  padding: 10px 17px;
}

.cartitem_deletebtn {
  padding: 10px 17px;
  color: red;
  background: #f4f4f4;
  border: 1px solid #171717;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.cartitem_deletebtn:hover,
.cartitem_deletebtn:focus,
.cartitem_deletebtn:active {
  background: #171717;
  transform: scale(1.2);
}

.cart_price {
  font-size: 1.3rem;
}

@media (max-width: 700px) {
  .cartitem_name {
    font-size: 0.8rem;
  }

  .cartitem_select,
  .cartitem_deletebtn {
    padding: 8px 13px;
  }

  .cartitem_price {
    font-size: 1rem;
  }
}

@media (max-width: 500px) {
  .cartitem_name {
    padding: 5px 8px;
  }

  .cartitem_price {
    font-size: 0.6rem;
  }
}
