:root {
  --green: #fc6924;
  --black: #18943d;
  --light-color: #666;
  --box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.1);
}


header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: #fff;
  padding: 1rem 7%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
  box-shadow: var(--box-shadow);
}

header .logo {
  color: var(--black);
  font-size: 2.5rem;
  font-weight: bolder;
}

header .logo i {
  color: var(--green);
}

header .navbar a {
  font-size: 1.7rem;
  border-radius: 0.5rem;
  padding: 0.5rem 1.5rem;
  color: var(--light-color);
  margin: 0 0.3rem;
}

header .navbar a.active,
header .navbar a:hover,
header .navbar a:focus {
  color: #fff;
  background: var(--green);
}

header .icons i,
header .icons a {
  cursor: pointer;
  margin-left: 0.5rem;
  height: 4.5rem;
  line-height: 4.5rem;
  width: 4.5rem;
  text-align: center;
  font-size: 1.7rem;
  color: var(--black);
  border-radius: 50%;
  background: #eee;
}

header .icons i:hover,
header .icons a:hover {
  color: #fff;
  background: var(--green);
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}

header .icons #menu-bars {
  display: none;
}

@media (max-width: 991px) {
  header {
    padding: 1rem 2rem;
  }
}

@media (max-width: 768px) {
  header .icons #menu-bars {
    display: inline-block;
  }

  header .navbar {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #fff;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.2);
    border-bottom: 0.1rem solid rgba(0, 0, 0, 0.2);
    padding: 1rem;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
            clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  }

  header .navbar.active {
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
  }

  header .navbar a {
    display: block;
    padding: 1.5rem;
    margin: 1rem;
    font-size: 2rem;
    background: #eee;
  }
}

* {
  padding: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  background: #f4f4f4;
}

img {
  width: 100%;
  object-fit: contain;
}

.cartpage {
  display: flex;
  max-width: 1300px;
  margin: 2rem auto;
  /* align-items: center; */
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding-top: 9rem;
}

.cartpage h2 {
  margin-bottom: 1rem;
}

.cartpage_left {
  flex: 0.7 1;
  margin-right: 1rem;
  background: transparent;
  padding: 1rem;
}

.cartpage_right {
  flex: 0.3 1;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.cartpage_right div {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding: 1rem;
}

.cartpage_right div:last-child {
  border: none;
}

.cartpage_info p {
  padding: 8px;
}

.cartpage_right div button {
  padding: 10px 17px;
  width: 100%;
  background: #171717;
  color: #f4f4f4;
  border: 1px solid #171717;
  cursor: pointer;
}

.cartpage_right div button:hover {
  opacity: 0.9;
}

.cartscreen_info > p{
  font-size: 1.5rem;
}

@media (max-width: 1320px) {
  .cartpage {
    max-width: 900px;
  }
}

@media (max-width: 960px) {
  .cartpage {
    max-width: 800px;
    flex-direction: column;
  }

  .cartpage_left {
    margin: 0rem;
  }

  .cartpage_right {
    margin: 1rem;
  }
}

.cartitem {
  width: 100%;
  padding: 1rem;
  margin: 8px;
  display: grid;
  grid-template-columns: 1fr 4fr 1fr 1fr 1fr;
  grid-gap: 8px;
  gap: 8px;
  background: #fff;
  border-radius: 2px;
  place-items: center;
}

.cartitem_name {
  text-decoration: none;
  color: #171717;
}

.cartitem_name > p {
  font-size: 1.3rem;
}

.cartitem_name:hover {
  color: #dc219e;
}

.cartitem_select {
  padding: 10px 17px;
}

.cartitem_deletebtn {
  padding: 10px 17px;
  color: red;
  background: #f4f4f4;
  border: 1px solid #171717;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

.cartitem_deletebtn:hover,
.cartitem_deletebtn:focus,
.cartitem_deletebtn:active {
  background: #171717;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.cart_price {
  font-size: 1.3rem;
}

@media (max-width: 700px) {
  .cartitem_name {
    font-size: 0.8rem;
  }

  .cartitem_select,
  .cartitem_deletebtn {
    padding: 8px 13px;
  }

  .cartitem_price {
    font-size: 1rem;
  }
}

@media (max-width: 500px) {
  .cartitem_name {
    padding: 5px 8px;
  }

  .cartitem_price {
    font-size: 0.6rem;
  }
}

.home .home-slider .slide {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  grid-gap: 2rem;
  gap: 2rem;
  padding-top: 9rem;
}

.home .home-slider .slide .content {
  flex: 1 1 45rem;
}

.home .home-slider .slide .image {
  flex: 1 1 45rem;
}

.home .home-slider .slide .image img {
  width: 100%;
}

.home .home-slider .slide .content span {
  color: var(--green);
  font-size: 2.5rem;
}

.home .home-slider .slide .content h3 {
  color: var(--black);
  font-size: 7rem;
}

.home .home-slider .slide .content p {
  color: var(--light-color);
